/*FILTER*/
.filter{
  width: 100%;
  height: 100%;
	overflow-y: scroll;
}
.filter::-webkit-scrollbar-track{
	-webkit-box-shadow: inset 0 0 6px rgba(0,0,0,0.10);
	border-radius: 7px;
	background-color: #93DEE8;
}
.filter::-webkit-scrollbar{
	width: 6px;
	background-color: #fff;
}
